@import '~zp-styles/common/_colors.scss';

.toast {
  position: fixed;
  top: calc(100vh / 4);
  left: 0px;
  right: 0px;
  display: flex;
  justify-content: center;
  background-color: $transparent;
  animation: fadeIn 0.5s linear;
  .toast__toast-content {
    padding: 20px 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.54);
    border-radius: 8px;
    min-width: 100px;
    max-width: 250px;
    .icon-general_check {
      font-size: 30px;
      margin-bottom: 10px;
      &:before {
        color: $white;
      }
    }
    .toast__toast-content__toast-msg {
      font-size: 15px;
      line-height: 20px;
      color: $white;
      text-align: center;
    }
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.toast.none {
  animation: fadeOut 0.5s linear;
}
